/* Full page layout and gradient background */
html, body {
  margin: 0;
  height: 100%;
  width: 100%;
}

.green-gradient, .blue-gradient {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  transition: background 0.8s ease-in-out;
  background-position: top right;
  background-size: cover;
}

.green-gradient {
  background: linear-gradient(to bottom left, lightgreen, green);
}

.blue-gradient {
  background: linear-gradient(to bottom left, lightblue, darkblue);
}

/* Flag container to switch languages */
.flag-container {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.flag {
  font-size: 30px;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.flag.selected {
  transform: scale(1.5);
}

/* Switch button at the top right */
.button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 20px;
  right: 20px;
}

.green-switch .slider {
  background-color: green;
}

.blue-switch .slider {
  background-color: blue;
}

.switch {
  position: relative;
  display: inline-block;
  width: 80px;
  height: 44px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: grey;
  transition: 0.4s;
  border-radius: 34px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 36px;
  width: 36px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
  border-radius: 50%;
}

input:checked + .slider:before {
  transform: translateX(36px);
}

/* Main layout container */
.main-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto 1fr;
  width: 100%;
  height: 100%;
  padding-top: 80px;
}

/* Floating text boxes */
.text-box {
  background-color: white;
  padding: 20px;
  border-radius: 50px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  max-width: fit-content;
  max-height: fit-content;
  margin: auto;
  transition: transform 0.1s ease-out;
}

/* Space A */
.space-a {
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  display: flex;
  justify-content: flex-start;
  padding-left: 20px;
}

/* Space B */
.space-b {
  grid-column: 2 / 3;
  grid-row: 1 / 2;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Space C */
.space-c {
  grid-column: 1 / 2;
  grid-row: 2 / 3;
  display: flex;
  justify-content: center;
}

.download-btn {
  padding: 10px 20px;
  font-size: 18px;
  background-color: #007bff;
  color: white;
  border-radius: 5px;
  cursor: pointer;
}
